import React from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import FeaturedPost from "./FeaturedPost";
import { makeStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import Footer from "../../components/Footer";
import Header from "../../components/Header";

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: 5,
    width: "80%",
    [theme.breakpoints.down("xs")]: {
      width: "100% !important", // Overrides inline-style
    },
  },
  headerImage: {
    backgroundImage:
      "url(https://cdn.pixabay.com/photo/2016/02/19/11/25/business-1209705_1280.jpg)",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center center",
    backgroundSize: "100%",
    objectFit: "stretch",
    height: 250,
    marginBottom: 10,
  },
}));

const featuredPosts = [
  {
    id: 1,
    title: "How Our Brain Learns?",
    date: "May 2, ",
    description:
      "Learning is just one of its many tasks. What I am going to elaborate is an incredibly protected thing that most people compare to walnuts. Yes, our brain.",
    image: "https://miro.medium.com/max/875/1*T218zGnF7bwqi0Q51ztNpQ.jpeg",
    imageText: "Image Text",
    linkText: "Continue reading…",
    author: "Enejan Balkanova",
    avatarPhoto:
      "data:image/jpeg;base64,",
  },
  {
    id: 2,
    title: "What Do We Mean by Thinking?",
    date: "Mar 28, 2018",
    description:
      "‘Thinking’ is the activity of human brain working of various views with the knowledge that has been stored in mind. So that means everything in your mind influence the way you think.",
    image: "https://miro.medium.com/max/375/1*va9auLTsoxemCwXzNLMXjA.jpeg",
    imgText: "main image description",
    linkText: "Continue reading…",
    author: "Enejan Balkanova",
    avatarPhoto:
      "data:image/jpeg;base64,",
  },
  {
    id: 3,
    title: "Motivation",
    date: "May 10, 2018",
    description:
      "Nowadays there are lots of social media accounts posting motivational, success quotes, personal trainings to motivate yourself, reach your goals and books to become successful and millionaire.",
    image: "https://miro.medium.com/max/651/1*ALwQkgCpJFyY4o2SqR-tAQ.jpeg",
    imageText: "Image Text",
    linkText: "Continue reading…",
    author: "Enejan Balkanova",
    avatarPhoto:
      "data:image/jpeg;base64,",
  },
];

export default function Blog() {
  const classes = useStyles();
  const post = require("./posts/post.json");
  return (
    <React.Fragment>
      <CssBaseline />
      <Header name="BLOG"></Header>
      <Container maxWidth="xl" className={classes.root}>
        <Box
          className={classes.headerImage}
          style={{
            backgroundImage: `url(https://cdn.pixabay.com/photo/2016/02/19/11/25/business-1209705_1280.jpg)`,
          }}
        ></Box>
        <Grid
          container
          spacing={3}
          direction="column"
          justify="space-evenly"
          alignItems="stretch"
        >
          {post.map((post) => (
            <FeaturedPost post={post}></FeaturedPost>
          ))}
        </Grid>
      </Container>
      <Footer></Footer>
    </React.Fragment>
  );
}
